/* ///////////////////////////////////////////////////////// */
/* //////////////////// CONTACT SECTION //////////////////// */
/* ///////////////////////////////////////////////////////// */
.contact-section {
  position: relative;
  padding: 6rem 8rem;

  /* Gradient overlay */
  background: linear-gradient(
    to bottom,
    rgb(94, 160, 191),
    rgba(194, 199, 201, 1)
  );

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rem;

  /* Make sure content appears above */
  z-index: 0;
  overflow: hidden;
}

/* Background video styling */

.background-video3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

  transform-origin: center; /* Keep video centered while scaling */

  z-index: -1;
  opacity: 1;
}

.video-overlay3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: linear-gradient(
    to bottom,
    rgb(167, 235, 239, 0.9),
    rgba(102, 165, 186, 0.5)
  );
  z-index: -1;
}

.contact-header {
  font-size: 2.6rem;
  letter-spacing: 2px;
  margin-bottom: 2rem;

  background: linear-gradient(to left, #1864ab, var(--primary-color));
  background-size: 150%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

/* Typing Animation / Style */
.typing6 {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid var(--background-color);
  width: 0;

  animation: typingContact 2.5s steps(18, end) forwards;
}

/* Description text above columns */
.contact-description {
  max-width: 900px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem; /* spacing between paragraphs */
  margin-bottom: 3rem; /* spacing below description */
}

.contact-description p,
.contact-info-text p {
  text-align: center;
  font-size: 1.4rem;
  line-height: 1.7;
  font-weight: 600;

  background: linear-gradient(to right, #1864ab, var(--primary-color));
  background-size: 150%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

/* ============================
   CONTACT FLEX CONTAINER
============================ */
.contact-flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6rem;
}

/* ============================
   RIGHT COLUMN
============================ */
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* aligns content from top */
  padding: 3rem 4rem;
  border-radius: 8px;
  gap: 6rem; /* consistent spacing between elements */
  max-width: 550px;
  background-color: rgba(30, 30, 47, 0.7);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.contact-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
}

/* Headers inside container */
.contact-container h3 {
  font-size: 1.8rem;
  letter-spacing: 1.5px;
  text-align: center;
  background: linear-gradient(to right, #abebef, var(--secondary-color));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 1rem;
}

/* Paragraphs inside container */
.contact-container p {
  font-size: 1.4rem;
  line-height: 1.6;
  color: var(--background-color);
  text-align: center;
}

.contact-email div {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.social-links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}

.social-links a ion-icon {
  font-size: 1.8rem;
  color: var(--secondary-color);
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-links a ion-icon:hover {
  transform: scale(1.2);
  color: var(--background-color);
}

/* ============================
   LEFT COLUMN
============================ */
.contact-stats {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  min-width: 320px;
  max-width: 600px;
  line-height: 1.5;
  /* background: rgba(255, 255, 255, 0.1); */
  padding: 2rem 4rem;
  /* box-shadow: 0 0 20px rgba(0, 191, 255, 0.15); */
  backdrop-filter: blur(4px);
}

.contact-stats h4 {
  font-size: 1.8rem;
  margin-bottom: 1.4rem;
  background: linear-gradient(to left, #abebef, var(--secondary-color));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  font-weight: 700;
}

.contact-stats ul {
  list-style: none;
  padding: 0;
}

.contact-stats li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.3rem;
  line-height: 1.8;
  font-weight: 500;
  cursor: pointer;

  transition: all 0.3s ease;
}

.contact-stats li span {
  display: inline-block;
  color: var(--secondary-color);
  font-size: 1.6rem;
  font-weight: 700;

  transition: all 0.3s ease;
}

.contact-stats li strong {
  transition: all 0.3s ease;
}

.contact-source {
  margin-top: 2rem;
  font-size: 1.2rem;
  background: linear-gradient(to right, #abebef, var(--secondary-color));
  background-clip: text;
  background-size: 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  font-style: italic;
}

.contact-stats li:hover {
  color: white;
}

.contact-stats li:hover strong {
  color: var(--secondary-color);
}

.contact-stats li:hover span {
  transform: scale(1.2);
  color: var(--highlight-color);
}

/* Make the Calendly overlay darker or gradient */
.calendly-overlay {
  background: rgba(255, 255, 255, 1) !important;

  transition: all 0.4s ease;
}

.calendly-popup iframe {
  overflow: hidden !important;
}

/* Optional: animate the popup open */
.calendly-popup {
  animation: fadeInUp 0.4s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========================================================= */
/* =============== CONTACT SECTION RESPONSIVENESS =========== */
/* ========================================================= */

/* 🖥️ Large screens (default) — your current styles apply */

/* 💻 Medium desktops and large tablets (≤1200px) */
@media (max-width: 1250px) {
  .contact-section {
    padding: 5rem 4rem;
  }

  .contact-flex-container {
    gap: 3rem;
  }

  .contact-header {
    font-size: 2.4rem;
  }

  .contact-description p {
    font-size: 1.3rem;
  }

  .contact-container {
    padding: 2.5rem;
    max-width: 500px;
  }

  .contact-stats {
    padding: 2rem 3rem;
  }
}

/* 📱 Tablets (≤992px) */
@media (max-width: 1030px) {
  .contact-section {
    padding: 4rem 6rem;
  }

  .contact-flex-container {
    flex-direction: column;
    align-items: center;
    gap: 4rem;
  }

  .contact-container,
  .contact-stats {
    max-width: 90%;
    width: 100%;
    padding: 2rem 1rem;
  }

  .contact-header {
    font-size: 2.3rem;
  }

  .contact-description p {
    font-size: 1.2rem;
  }

  .contact-source {
    font-size: 1.1rem;
  }
}

/* 📲 Small tablets / large phones (≤768px) */
@media (max-width: 800px) {
  .contact-section {
    padding: 4rem 3rem;
  }

  .contact-header {
    font-size: 2.2rem;
    letter-spacing: 1px;
  }

  .contact-container {
    gap: 4rem;
    padding: 2rem;
  }

  .contact-stats {
    padding: 1.8rem 2rem;
  }

  .contact-container h3 {
    font-size: 1.6rem;
  }

  .contact-container p,
  .contact-stats li {
    font-size: 1.2rem;
  }

  .contact-stats h4 {
    font-size: 1.5rem;
  }

  .contact-description p {
    font-size: 1.15rem;
  }

  .contact-source {
    font-size: 1rem;
  }
}

/* 📞 Mobile phones (≤576px) */
@media (max-width: 650px) {
  .contact-section {
    padding: 3rem 2.5rem;
  }

  .contact-header {
    font-size: 2rem;
  }

  .contact-description {
    gap: 1rem;
  }

  .contact-description p {
    font-size: 1.1rem;
    line-height: 1.6;
  }

  .contact-flex-container {
    gap: 3rem;
  }

  .contact-container,
  .contact-stats {
    padding: 1.5rem;
    width: 100%;
    border-radius: 6px;
  }

  .contact-stats li {
    font-size: 1.1rem;
  }

  .social-links a ion-icon {
    font-size: 1.5rem;
  }

  .secondary-btn {
    font-size: 1.2rem;
    padding: 0.8rem 1.5rem;
  }

  .contact-stats li {
    flex-wrap: wrap;
    line-height: 1.6;
  }

  .contact-source {
    font-size: 0.9rem;
  }
}

/* 📱 Extra small phones (≤400px) */
@media (max-width: 475px) {
  .contact-section {
    padding: 2.5rem 2rem;
  }
  .contact-header {
    font-size: 1.9rem;
  }

  .contact-description p {
    font-size: 1rem;
  }

  .contact-container p,
  .contact-stats li {
    font-size: 1rem;
  }

  .contact-stats h4 {
    font-size: 1.4rem;
  }

  .contact-source {
    font-size: 0.8rem;
  }
}
