/* FAQ Section Styles */
.faq-section {
  background: linear-gradient(to top, #f0f8ff, #ffffff);
  padding: 6rem 8rem;
}

/* Wrapper with max width and spacing */
.faq-wrapper {
  max-width: 1300px;
  margin: 0 auto;
  display: flex;
  /* align-items: center; */
  justify-content: center;
  flex-direction: column;
  gap: 6rem;
}

/* Section title styling */
.faq-wrapper h2 {
  font-size: 2.6rem;
  letter-spacing: 2.5px;
  display: flex;
  align-self: center;
  color: var(--secondary-color);
}

/* Typing Animation / Style */
.typing5 {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid var(--background-color);
  width: 0;

  animation: typingFaq 4s steps(30, end) forwards;
}

/* List of FAQ items */
.faq-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

/* Each FAQ card */
.faq-item {
  border-radius: 4px;
  padding: 1rem 1.5rem;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s ease;
}

/* Hover effect */
.faq-item:hover {
  box-shadow: inset 0 0 0px 4px rgba(0, 191, 255, 0.81);
}

/* FAQ question button (full width) */
.faq-question-btn {
  font-size: 1.4rem;
  letter-spacing: 2px;
  color: var(--primary-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; /* Full width */
  background: none;
  border: none;
  padding: 0;
  text-align: left;
  cursor: pointer;
  transition: color 0.3s ease;
}

/* Arrow rotation transition */
.faq-arrow {
  font-size: 1.2rem;
  color: var(--primary-color);
  transform: rotate(0deg);
  transition: transform 0.4s ease;
}

/* Rotate arrow when active */
.faq-item.active .faq-arrow {
  transform: rotate(90deg);
}

/* FAQ answer container */
.faq-answer {
  border-top: 1px solid transparent;
  padding: 0 0.5rem;
  font-size: 1.4rem;
  line-height: 1.6;
  letter-spacing: 1.5px;
  margin-top: 1rem;

  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.5s ease, opacity 0.4s ease, padding 0.4s ease;
}

/* Expand answer when active */
.faq-item.active .faq-answer {
  border-top: 1px solid rgb(233, 233, 233);
  padding: 1rem 0.5rem;
  max-height: 500px;
  opacity: 1;
}

/* ///////////////////////////////////////////////////////// */
/* ////////////////// FAQ MEDIA QUERIES ///////////////////// */
/* ///////////////////////////////////////////////////////// */

/* ---------- Large Screens (≥1600px) ---------- */
@media (min-width: 1600px) {
  .faq-section {
    padding: 8rem 12rem;
  }

  .faq-wrapper h2 {
    font-size: 3rem;
  }

  .faq-question-btn {
    font-size: 1.6rem;
  }

  .faq-answer {
    font-size: 1.5rem;
  }
}

/* ---------- Medium Desktops (1200px–1599px) ---------- */
@media (max-width: 1599px) {
  .faq-section {
    padding: 6rem 10rem;
  }

  .faq-wrapper {
    gap: 5rem;
  }

  .faq-wrapper h2 {
    font-size: 2.6rem;
  }

  .faq-question-btn {
    font-size: 1.4rem;
  }

  .faq-answer {
    font-size: 1.3rem;
  }
}

/* ---------- Tablets / Small Desktops (992px–1199px) ---------- */
@media (max-width: 1199px) {
  .faq-section {
    padding: 5rem 8rem;
  }

  .faq-wrapper h2 {
    font-size: 2.4rem;
  }

  .faq-question-btn {
    font-size: 1.3rem;
    letter-spacing: 1.5px;
  }

  .faq-answer {
    font-size: 1.25rem;
  }
}

/* ---------- Tablets (768px–991px) ---------- */
@media (max-width: 991px) {
  .faq-section {
    padding: 5rem 6rem;
  }

  .faq-wrapper {
    gap: 4rem;
  }

  .faq-wrapper h2 {
    font-size: 2.3rem;
    letter-spacing: 2px;
  }

  .faq-question-btn {
    font-size: 1.2rem;
  }

  .faq-answer {
    font-size: 1.15rem;
    line-height: 1.5;
  }
}

/* ---------- Large Mobile (576px–767px) ---------- */
@media (max-width: 767px) {
  .faq-section {
    padding: 4rem;
  }

  .faq-wrapper {
    gap: 3rem;
  }

  .faq-wrapper h2 {
    font-size: 2.2rem;
  }

  .faq-question-btn {
    font-size: 1.1rem;
    letter-spacing: 1px;
  }

  .faq-answer {
    font-size: 1.05rem;
  }
}

/* ---------- Mobile (≤575px) ---------- */
@media (max-width: 625px) {
  .faq-section {
    padding: 3.5rem 2.5rem;
  }

  .faq-wrapper {
    gap: 2.5rem;
  }

  .faq-wrapper h2 {
    font-size: 1.8rem;
  }

  .faq-question-btn {
    font-size: 1rem;
    letter-spacing: 0.5px;
  }

  .faq-arrow {
    font-size: 1rem;
  }

  .faq-answer {
    font-size: 1rem;
    line-height: 1.4;
  }
}

/* ---------- Small Mobile (≤450px) ---------- */
@media (max-width: 520px) {
  .faq-section {
    padding: 3rem 2rem;
  }

  .faq-wrapper h2 {
    font-size: 1.6rem;
  }
}

/* ---------- Tiny Mobile (≤360px) ---------- */
@media (max-width: 445px) {
  .faq-section {
    padding: 2.5rem 1.5rem;
  }

  .faq-wrapper h2 {
    font-size: 1.3rem;
  }

  .faq-question-btn {
    font-size: 0.95rem;
  }

  .faq-answer {
    font-size: 0.95rem;
  }
}
